.nab-regular-upgrade-form--loading .components-popover__content {
  display: flex;
  min-height: 5em;
  justify-content: center;
  align-items: center;
}
.nab-regular-upgrade-form__product-container {
  min-width: 30em;
  margin: 1em;
  text-align: left;
}
.nab-regular-upgrade-form .components-base-control__label {
  font-size: 1.3em;
}
.nab-regular-upgrade-form .components-radio-control__option {
  align-items: flex-start;
  margin-top: 1em;
  display: flex;
}
.nab-regular-upgrade-form__product {
  display: inline-block;
}
.nab-regular-upgrade-form__product-price {
  margin-left: 0.5em;
}
.nab-regular-upgrade-form__product-description {
  display: block;
}
.nab-regular-upgrade-form__button-container {
  padding: 0 1em 1em;
  text-align: right;
}

.nab-enterprise-upgrade-form--loading .components-popover__content {
  display: flex;
  min-height: 5em;
  justify-content: center;
  align-items: center;
}
.nab-enterprise-upgrade-form__container {
  min-width: 20em;
  margin: 1em;
  text-align: left;
}
.nab-enterprise-upgrade-form .components-base-control__label {
  font-size: 1.3em;
}
.nab-enterprise-upgrade-form .components-radio-control__option {
  align-items: flex-start;
  margin-top: 1em;
  display: flex;
}
.nab-enterprise-upgrade-form__product {
  display: inline-block;
}
.nab-enterprise-upgrade-form__product-price {
  margin-left: 0.5em;
}
.nab-enterprise-upgrade-form__product-description {
  display: block;
}
.nab-enterprise-upgrade-form__button-container {
  padding: 0 1em 1em;
  display: flex;
  gap: 0.5em;
  justify-content: flex-end;
}